ul.timeline {
  li.timeline-item {
    position: relative;
    &:after {
      content: '';
      display: block;
      width: 11px;
      height: 11px;
      border: 2px solid #97a3b4;
      background-color: white;
      border-radius: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto 0;
      left: 10px;
      @media (min-width: 767px) {
        left: 20px;
      }
    }
    &:before {
      content: '';
      margin: auto 0;
      top: 0;
      bottom: 0;
      position: absolute;
      height: 100%;
      width: 2px;
      background-color: #97a3b4;
      display: block;
      left: 15px;
      @media (min-width: 767px) {
        left: 25px;
      }
    }
    &:last-of-type {
      &:before {
        // height: 50%;
        // top:0;
        // margin: 0
        border-radius: 0 0 100% 100%;
      }
    }
    &:first-of-type {
      &:before {
        border-radius: 100% 100% 0 0;
        // height: 50%;
        // top: auto;
        // bottom: 0 !important;
        // margin: 0;
      }
    }
  }
}
